<template>
    <div>
        <div class="chapter-bg">
            <div class="route-back" @click="routeBack">
                <van-icon name="arrow-left" />
                <span>{{ $route.params.title }}</span>
            </div>
            <div class="chapter-title">{{ currentChapter.title }}</div>
            <ul class="chapter-content">
                <li v-for="(item, index) in currentCPContent" :key="index">
                    <p>{{ item }}</p>
                </li>
            </ul>
            <div class="btns">
                <span class="prev" v-if="prevIndex > -1" @click="prevLink"
                    >上一章</span
                >
                <span class="back" @click="routeToChapter">返回目录</span>
                <span
                    class="next"
                    v-if="
                        nextIndex < chapterList.length &&
                            !chapterList[nextIndex].isVip
                    "
                    @click="nextLink"
                    >下一章</span
                >
            </div>
        </div>
    </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import types from "@/store/types.js";
import store from "@/store/index.js";
import { AnalyzeArticle } from "@/api/api.js";
export default {
    name: "Read",
    data() {
        return {
            prevIndex: 0,
            currentIndex: 0,
            nextIndex: 0,
            currentCPContent: [],
        };
    },
    computed: {
        ...mapState({
            chapterList: (state) => state.chapterList,
            currentChapter: (state) => state.currentChapter,
        }),
    },
    methods: {
        ...mapActions([types.GET_CURREN_BOOK_LINK]),
        routeBack() {
            this.$router.push({ name: "Bookshelf" });
        },
        routeToChapter() {
            this.$router.push({
                name: "BookChapter",
                params: { id: this.$route.params.id },
            });
        },
        prevLink() {
            if (this.prevIndex == -1) {
                return;
            } else {
                this.nextIndex = this.currentIndex;
                this.currentIndex = this.prevIndex;
                this.prevIndex -= 1;
            }
        },
        nextLink() {
            if (this.nextIndex > this.chapterList.length) {
                return;
            } else if (this.chapterList[this.nextIndex].isVip) {
                return;
            } else {
                this.prevIndex = this.currentIndex;
                this.currentIndex = this.nextIndex;
                this.nextIndex += 1;
            }
        },
    },
    watch: {
        currentChapter(val) {
            if (val.cpContent) {
                this.currentCPContent = AnalyzeArticle(val.cpContent);
            }
        },
        currentIndex(val) {
            this.$router.push({
                name: "Read",
                params: {
                    title: this.$route.params.title,
                    id: this.$route.params.id,
                    index: val,
                },
            });
            this[types.GET_CURREN_BOOK_LINK]({
                link: this.chapterList[val].link,
            });
        },
    },
    beforeRouteLeave(to, from, next) {
        let currentshelf = JSON.parse(localStorage.getItem("bookshelf"));
        if (
            currentshelf &&
            currentshelf.data["book-" + this.$route.params.id]
        ) {
            currentshelf.data[
                "book-" + this.$route.params.id
            ].index = this.$route.params.index;
            localStorage.setItem("bookshelf", JSON.stringify(currentshelf));
            next();
        } else {
            this.$dialog
                .confirm({
                    title: "加入书架",
                    message: "喜欢本书就加入书架吧",
                    messageAlign: "left",
                    confirmButtonText: "加入书架",
                    confirmButtonColor: "#91DCAE",
                    cancelButtonText: "不用了",
                    cancelButtonColor: "#4C5055",
                })
                .then(() => {
                    currentshelf.data["book-" + this.$route.params.id] = {
                        title: this.$route.params.title,
                        id: this.$route.params.id,
                        index: this.$route.params.index,
                    };
                    localStorage.setItem(
                        "bookshelf",
                        JSON.stringify(currentshelf)
                    );
                    next();
                })
                .catch(() => {
                    next();
                });
        }
    },
    beforeRouteEnter(to, from, next) {
        store.dispatch(types.GET_ONE_BOOK_LINK, {
            id: to.params.id,
            index: to.params.index,
        });

        let prevIndex = 0;
        let nextIndex = 0;
        let currentIndex = Number(to.params.index);
        if (currentIndex > 0) {
            prevIndex = currentIndex - 1;
        } else {
            prevIndex = currentIndex;
        }
        if (currentIndex < store.state.chapterList.length) {
            nextIndex = currentIndex + 1;
        } else {
            nextIndex = currentIndex;
        }
        next((vm) => {
            vm.currentIndex = currentIndex;
            vm.prevIndex = prevIndex;
            vm.nextIndex = nextIndex;
        });
    },
};
</script>

<style lang="less" scoped>
.chapter-bg {
    background-color: #c4b395;
    text-align: left;
    padding: 10px 10px 5px;
    .route-back {
        display: flex;
        align-items: center;
        padding: 5px 0;
        span {
            font-size: 12px;
        }
    }
    .chapter-title {
        font-size: 22px;
        font-weight: bold;
        margin-top: 5px;
    }
    .chapter-content {
        font-size: 14px;
        line-height: 24px;
        margin-top: 20px;
        p {
            margin: 0;
            margin-top: 5px;
            text-indent: 2em;
        }
    }
    .btns {
        display: flex;
        text-align: center;
        align-items: center;
        margin-top: 10px;
        span {
            flex: 4;
            padding: 5px;
            background-color: #e4393c;
            border-radius: 40px;
            margin: 5px;
            color: #eee;
        }
    }
}
</style>
